@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap);
html,
body {
  background: rgb(0, 92, 184);
  background: linear-gradient(
      135deg,
      rgb(0, 92, 184) 0%,
      rgba(0, 45, 98, 1) 50%,
      rgba(0, 13, 28, 1) 100%
    )
    fixed;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0;
  padding: 0;
}

.App {
  color: #fff;
  text-align: center;
}

.Home {
  text-align: left;
}

.UkanaHeader,
.UkanaHeaderJp {
  display: inline-block;
  font-size: 5rem;
  font-weight: 500;
  margin: 0;
  margin-left: 1rem;
}

.UkanaHeaderJp {
  color: #bf1919;
  font-weight: 300;
}

.Subheader {
  margin: 0;
  margin-left: 1rem;
  font-style: italic;
}

.SubheaderStart {
  font-weight: 500;
  margin-left: 1rem;
}

.Login {
  background-position: bottom;
  background-repeat: repeat-x;
  background-size: 10%;
  height: 85vh;
}

.welcome,
.login-message {
  margin: 0;
  text-align: center;
}

.welcome {
  font-size: 2rem;
}

.login-message {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.login-form {
  -webkit-animation: loginRev 50s infinite;
          animation: loginRev 50s infinite;
  background: rgb(74, 0, 143);
  background: linear-gradient(
    60deg,
    rgba(74, 0, 143, 1) 0%,
    rgba(74, 0, 143, 1) 17%,
    rgb(103, 0, 0) 17%,
    rgba(103, 0, 0, 1) 22%,
    rgba(0, 39, 84, 1) 22%,
    rgba(0, 39, 84, 1) 51%,
    rgba(103, 0, 0, 1) 51%,
    rgba(103, 0, 0, 1) 56%,
    rgba(0, 21, 46, 1) 56%,
    rgba(0, 21, 46, 1) 84%,
    rgba(103, 0, 0, 1) 84%,
    rgba(103, 0, 0, 1) 89%,
    rgba(74, 0, 143, 1) 89%,
    rgba(74, 0, 143, 1) 100%
  );
  background-size: 300%;
  background-position: -500px;
  border-radius: 25px;
  line-height: 3rem;
  margin: auto;
  padding: 1rem;
  text-align: center;
  width: -webkit-max-content;
  width: max-content;
}

.login-input {
  display: inline-block;
  width: 45%;
  font-family: "Noto Sans JP", sans-serif;
}

.input {
  border: 0;
  border-radius: 12px;
  color: #000;
  font-size: 0.94rem;
  height: 1.5rem;
  text-align: center;
}
.input::-webkit-input-placeholder {
  color: #666;
}
.input:-ms-input-placeholder {
  color: #666;
}
.input::placeholder {
  color: #666;
}

.login-form-button {
  background-color: #4cbf5d;
  border: 0;
  border-radius: 25px;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.5rem;
}

.login-form-button:hover {
  -webkit-animation: loginGlowHover 1s infinite;
          animation: loginGlowHover 1s infinite;
  cursor: pointer;
}

@-webkit-keyframes loginRev {
  from {
    background-position: -200px;
  }
  to {
    background-position: 500px;
  }
}

@keyframes loginRev {
  from {
    background-position: -200px;
  }
  to {
    background-position: 500px;
  }
}

@-webkit-keyframes loginGlowHover {
  50% {
    outline-color: #27a0eb;
    outline-offset: 3px;
    outline-style: solid;
    outline-width: 3px;
  }
  100% {
    background-color: #15e673;
    outline-offset: 6px;
    outline-color: #ffb1b1;
  }
}

@keyframes loginGlowHover {
  50% {
    outline-color: #27a0eb;
    outline-offset: 3px;
    outline-style: solid;
    outline-width: 3px;
  }
  100% {
    background-color: #15e673;
    outline-offset: 6px;
    outline-color: #ffb1b1;
  }
}

.Navbar {
  background: #090645;
  padding: 0;
  text-align: left;
  width: auto;
}

a {
  color: #fff;
  margin: 0;
  padding: 0;
