}

.ukanaLogo {
  -webkit-animation: logoHoverAfter 0.5s forwards;
          animation: logoHoverAfter 0.5s forwards;
  border: 3px solid transparent;
  border-radius: 12px;
  margin: 1vw 0 0.4vw 1vw;
  width: 1.9rem;
}

.ukanaLogo:hover {
  -webkit-animation: logoHover 0.5s forwards;
          animation: logoHover 0.5s forwards;
  border: 3px solid #cdc618;
  border-radius: 12px;
}

@-webkit-keyframes logoHover {
  0% {
    border-color: transparent transparent transparent transparent;
  }
  25% {
    border-color: #cdc618 transparent transparent transparent;
  }
  50% {
    border-color: #cdc618 #cdc618 transparent transparent;
  }
  75% {
    border-color: #cdc618 #cdc618 #cdc618 transparent;
  }
  100% {
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
  }
}

@keyframes logoHover {
  0% {
    border-color: transparent transparent transparent transparent;
  }
  25% {
    border-color: #cdc618 transparent transparent transparent;
  }
  50% {
    border-color: #cdc618 #cdc618 transparent transparent;
  }
  75% {
    border-color: #cdc618 #cdc618 #cdc618 transparent;
  }
  100% {
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
  }
}

@-webkit-keyframes logoHoverAfter {
  0% {
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
  }
  25% {
    border-color: #cdc618 #cdc618 #cdc618 transparent;
  }
  50% {
    border-color: #cdc618 #cdc618 transparent transparent;
  }
  75% {
    border-color: #cdc618 transparent transparent transparent;
  }
  100% {
    border-color: transparent transparent transparent transparent;
  }
}

@keyframes logoHoverAfter {
  0% {
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
  }
  25% {
    border-color: #cdc618 #cdc618 #cdc618 transparent;
  }
  50% {
    border-color: #cdc618 #cdc618 transparent transparent;
  }
  75% {
    border-color: #cdc618 transparent transparent transparent;
  }
  100% {
    border-color: transparent transparent transparent transparent;
  }
}

