  }
  50% {
    border-color: #cdc618 #cdc618 #fff #fff;
  }
  75% {
    border-color: #cdc618 #cdc618 #cdc618 #fff;
  }
  100% {
    background: #2a50a0;
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
    color: #cdc618;
  }
}

@-webkit-keyframes numberHoverAfter {
  0% {
    background: #2a50a0;
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
    color: #cdc618;
  }
  25% {
    border-color: #cdc618 #cdc618 #cdc618 #fff;
  }
  50% {
    border-color: #cdc618 #cdc618 #fff #fff;
  }
  75% {
    border-color: #cdc618 #fff #fff #fff;
  }
  100% {
    border-color: #fff #fff #fff #fff;
